import { Injectable } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';

@Injectable({
  providedIn: 'root'
})
export class MessageService {

  constructor(public snackBar: MatSnackBar) { }

  showError(msg) {
    this.snackBar.open(msg, "Okay", {duration: 2000});
  }

  showSuccess(msg) {
    this.snackBar.open(msg, "Okay", {duration: 2000});
  }

}
